<template>
  <div class="upload">
    <el-form ref="form" :model="form" label-width="80px">
      <el-form-item label="昵称">
        <el-input :disabled="isupload" v-model="form.name"></el-input>
      </el-form-item>
      <!--  -->
      <el-form-item label="编号">
        <el-input :disabled="isupload" v-model="form.id"></el-input>
      </el-form-item>
      <!--  -->
      <el-form-item label="印记">
        <el-select :disabled="isupload" v-model="form.imprint" multiple placeholder="请选择活印记">
          <el-option v-for="(t, i) in tag" :key="i" :label="t" :value="t"></el-option>
        </el-select>
      </el-form-item>
      <!--  -->
      <el-form-item label="状态" v-show="isupload">
        <el-switch v-model="form.complete" active-text="已完成" inactive-text="未完成"> </el-switch>
      </el-form-item>
      <!--  -->
      <el-form-item label="佣金" v-show="isupload">
        <el-input v-model="form.money"></el-input>
      </el-form-item>
      <!--  -->
      <el-form-item>
        <el-button type="primary" @click="dess">{{ isupload ? '立即更新' : '立即创建' }}</el-button>
        <el-button @click="backtrack">取消</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
import { UserDataUpload, userItemUpload } from '@/utils/upload'
import { mapMutations } from 'vuex'
import { getItem } from '@/utils/db'
export default {
  name: 'PracticeUpload',
  data() {
    return {
      form: {
        name: '',
        id: '',
        imprint: [],
      },
      tag: ['卓越', '传奇', '绝世', '残局'],
      isupload: false,
    }
  },
  methods: {
    ...mapMutations(['addItem', 'uploadItem']),
    dess() {
      if (this.isupload) {
        const result = userItemUpload(this.form)
        this.uploadItem(result)
      } else {
        if (this.form.name && this.form.id) {
          UserDataUpload(this.form)
          this.addItem(this.form)
          this.form = {}
        }
      }
      this.backtrack()
    },
    backtrack() {
      this.$router.replace('/')
    },
  },
  beforeRouteEnter(to, from, next) {
    const hash = to.params.hash || to.query.hash
    if (hash) {
      getItem(hash).then((item) => {
        next((vm) => {
          vm.form = item
          vm.isupload = true
        })
      })
    } else {
      next()
    }
  },
  beforeRouteLeave(to, from, next) {
    this.isupload = false
    next()
  },
}
</script>

<style lang="css" scoped>
.upload {
  width: 430px;
}
</style>