<template>
  <div class="item" :style="{ gridTemplateColumns: xx }" ref="itemRefs">
    <span>{{ Index }}</span>
    <span class="date">{{ 0 }}</span>
    <span class="name">{{ FormItem.name }}</span>
    <span>{{ FormItem.id }}</span>
    <div class="tag">
      <span v-for="(t, i) in FormItem.imprint" :key="i">{{ t }}</span>
    </div>
    <div class="btn">
      <button type="button" @click="demo(FormItem)" :class="{ active: FormItem.complete }">
        {{ FormItem.complete ? '已结单' : '未结单' }}
      </button>
      <button type="button" @click="removeItem(FormItem.hash)">删除</button>
    </div>
  </div>
</template>

<script>
import { mapMutations } from 'vuex'
export default {
  name: 'PracticeItem',
  props: {
    FormItem: Object,
    Index: Number,
  },
  methods: {
    ...mapMutations(['removeItem', 'uploadItem']),
    demo(item) {
      this.$router.replace({
        path: `/upload/`,
        query: {
          hash: item.hash,
        },
      })
    },
  },
  computed: {
    xx() {
      return 1
    },
  },
}
</script>

<style lang="css" scoped>
.item {
  width: 100%;
  display: grid;
  grid-template-columns: 32px 84px 112px 81px 150px 144px;
  grid-template-rows: 1fr;
  grid-column-gap: 10px;
  grid-row-gap: 0px;
  justify-items: center;
  align-items: center;
  height: 3em;
  margin-bottom: 1.1em;
}

.date {
  font-size: 15px;
}

.item:last-child {
  margin-bottom: 0 !important;
}

.btn {
  width: 100%;
  display: flex;
  gap: 1em;
  justify-content: flex-end;
  align-items: center;
}
.btn button {
  width: 64px;
  height: 2.5em;
  cursor: pointer;
  border-radius: 0.6em;
  font-size: small;
}
.btn button:nth-of-type(1) {
  background-color: #e2f2ff;
  color: #1e99ff;
}
.btn button:nth-of-type(2) {
  background-color: #ffdbdb;
  color: #ff7979;
}

.active {
  background-color: #d1f6e0 !important;
  color: #2ed573 !important;
}

.tag span {
  margin: 0.3em;
}
</style>