import md5 from 'md5-js';
import dayjs from 'dayjs';
import { setupItem } from '@/utils/db.js';

export function UserDataUpload(data) {
  const timer = new Date().getTime();
  var user = {
    ...data,
    date: dayjs(timer).format('YYYY-MM-DD'),
    timestamp: timer,
    duration: '',
    complete: Boolean(false),
    end: '',
  };

  user.hash = md5(JSON.stringify(user));
  setupItem(user);

  return user;
}
export function userItemUpload(params) {
  const { timestamp, duration } = params;
  const end = new Date().getTime();
  const user = {
    ...params,
    duration: duration > 0 ? duration : end - timestamp,
    end,
  };

  setupItem(user);

  return user;
}
