import Vue from 'vue';
import Vuex from 'vuex';
import { deleteItem } from '@/utils/db';

Vue.use(Vuex);

const mutations = {
  removeItem(state, hash) {
    if (confirm('是否要删除该数据?')) {
      state.table = state.table.filter(item => item.hash != hash);
      deleteItem(hash);
    }
  },
  // 更新
  uploadItem(state, value) {
    state.table = state.table.map(item => {
      if (item.hash == value.hash) {
        return value;
      } else {
        return item;
      }
    });
  },
  // 前追加
  addItem(state, item) {
    state.table = [item, ...state.table];
  },
};

const state = {
  table: [],
};

export default new Vuex.Store({
  mutations,
  state,
});
