import Dexie from 'dexie';

export const db = new Dexie('myDatabase');
db.version(1).stores({
  hpjy: '&hash, name, duration',
  test: '&hash, name, duration',
});

const name_ = 'hpjy';

export async function getterItem() {
  const result = await db[name_].toArray();

  return quickSort(quickSort(result, 'timestamp'), 'complete');
}

export function setupItem(params) {
  /**
   * 向数据库中插入或更新一个项目。
   * @param {Object} params - 要插入或更新到数据库中的项目参数。
   * @param {string} params.hash - 项目的唯一哈希值，用作数据库中的键。
   * @param {Object} params.data - 项目的数据，将被存储在数据库中。
   * @returns {Promise} - 一个Promise对象，解析为插入或更新操作的结果。
   */
  return db[name_].put(params);
}

export function deleteItem(hash) {
  return db[name_].delete(hash);
}

export async function getItem(hash) {
  return await db[name_].get(hash);
}

function quickSort(arr, key) {
  if (arr.length <= 1) {
    return arr;
  }

  var pivotIndex = Math.floor(arr.length / 2);

  var pivot = arr.splice(pivotIndex, 1)[0];

  var left = [];

  var right = [];

  for (var i = 0; i < arr.length; i++) {
    if (arr[i][key] < pivot[key]) {
      left.push(arr[i]);
    } else {
      right.push(arr[i]);
    }
  }

  return quickSort(left).concat([pivot], quickSort(right));
}
