<template>
  <div class="income">
    <card v-for="(v, i) in p" :key="i" :item="v" />
  </div>
</template>

<script>
// import { mapState } from 'vuex'
import card from '@/base/card.vue'
import { getterItem } from '@/utils/db'
export default {
  name: 'Income',
  data() {
    return {
      p: [],
    }
  },
  components: {
    card,
  },
  methods: {
    methodName() {
      const arr = [60, 100, 150]
      const b = Array.from(
        { length: Math.floor(Math.random() * (90 - 30 + 1) - 30) },
        () => arr[Math.floor(Math.random() * arr.length)]
      )
      const sum = b.reduce((acc, curr) => acc + curr, 0)
      return {
        total: sum,
        average: b.length,
      }
    },
  },
  created() {
    var objects = []
    for (let index = 24; index < 30; index++) {
      var a = {
        cover: require(`@/assets/${index}.webp`),
        sequence: index,
        fake: this.methodName(),
      }
      objects.push(a)
    }

    // 对数组进行排序，按照 sequence 属性升序排列
    objects.sort((a, b) => b.sequence - a.sequence)

    this.p = objects
  },
}
</script>

