import Vue from 'vue';
import VueRouter from 'vue-router';

Vue.use(VueRouter);

import list from '@/components/list.vue';
import upload from '@/components/upload.vue';
import income from '@/View/Income.vue';
export default new VueRouter({
  routes: [
    {
      path: '/',
      component: list,
    },
    {
      path: '/upload',
      component: upload,
    },
    {
      path: '/income',
      component: income,
    },
  ],
});
